@import url('https://fonts.googleapis.com/css2?family=Varela+Round&display=swap');

::-webkit-scrollbar {
  width: 12px;
}
::-webkit-scrollbar-thumb {
  background-color: transparent; 
  border-radius: 6px;
  color: transparent;
}
*::-webkit-scrollbar-track {
  background: black; 
}
.App {
  text-align: center;
}

.App-header {
  background: rgb(0, 0, 0);
  color: white;
  padding: 20px;
  font-family: 'Varela Round', sans-serif;
  font-size: 25px;
  user-select: none;
  text-shadow: 2px 1px rgb(7, 114, 253);
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px; 
  background-color: #090909; 
  padding: 50px; 
}

.image-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px; 
  background-color: #000000;
  box-shadow: 0 0 5px rgb(7, 114, 253); 
}

.image img{
  max-width: 95%;
  height: auto;
  margin-top: 2.5%;
  border-radius: 10px;
}

.image-card .button-container .button {
  background: #007bff;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 10px; 
  margin: 6% 0 8% 0;
  cursor: pointer;
  font-weight: bold;
  font-family: 'Varela Round';
}

@media (max-width: 768px) {
  .grid-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .image-card {
    width: 45%; 
    margin: 10px; 
  }
}


